<h1 id="webpack" tabindex="-1"><a class="header-anchor" href="#webpack" aria-hidden="true">#</a> Webpack</h1>
<p>安装后，可以通过<code>CLI</code>或者<code>API</code>和webpack进行交互</p>
<h2 id="什么是webpack" tabindex="-1"><a class="header-anchor" href="#什么是webpack" aria-hidden="true">#</a> 什么是webpack</h2>
<p>现代Javascript应用程序的静态模块<code>打包器</code>。</p>
<p>!&gt; Webpack被用来编译JavaScript模块，将一些独立的模块，比如很多个js文件打包成一个js文件</p>
<p>先理解<code>bundle</code>这个单词
n. 捆；束；包；
v. 捆；把 ... 扎成一捆</p>
<p>webpack是一个<code>打包工具</code>，可以将项目中所有的<code>资源</code>打包，这些资源包含<code>css样式</code>，<code>js脚本</code>，<code>图片</code>。</p>
<h2 id="如何使用它" tabindex="-1"><a class="header-anchor" href="#如何使用它" aria-hidden="true">#</a> 如何使用它</h2>
<p>module.exports</p>
<p>Webpack is a modular build tool that has two sets of functionality</p>
<ol>
<li>Loaders</li>
<li>plugins</li>
</ol>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">import</span> webpack <span class="token keyword">from</span> <span class="token string">'webpack'</span><span class="token punctuation">;</span>

<span class="token comment">// Try the environment variable, otherwise use root</span>
<span class="token keyword">const</span> <span class="token constant">ASSET_PATH</span> <span class="token operator">=</span> process<span class="token punctuation">.</span>env<span class="token punctuation">.</span><span class="token constant">ASSET_PATH</span> <span class="token operator">||</span> <span class="token string">'/'</span><span class="token punctuation">;</span>

<span class="token keyword">export</span> <span class="token keyword">default</span> <span class="token punctuation">{</span>
  output<span class="token operator">:</span> <span class="token punctuation">{</span>
    publicPath<span class="token operator">:</span> <span class="token constant">ASSET_PATH</span>
  <span class="token punctuation">}</span><span class="token punctuation">,</span>

  plugins<span class="token operator">:</span> <span class="token punctuation">[</span>
    <span class="token comment">// This makes it possible for us to safely use env vars on our code</span>
    <span class="token keyword">new</span> <span class="token class-name">webpack<span class="token punctuation">.</span>DefinePlugin</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
      <span class="token string">'process.env.ASSET_PATH'</span><span class="token operator">:</span> <span class="token constant">JSON</span><span class="token punctuation">.</span><span class="token function">stringify</span><span class="token punctuation">(</span><span class="token constant">ASSET_PATH</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span><span class="token punctuation">)</span>
  <span class="token punctuation">]</span>
<span class="token punctuation">}</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><p>require是webpack的关键字</p>
